.recipe {
  border-radius: 10px;
  box-shadow: 0 5px 20px rgb(71, 71, 71);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: white;
  align-items: center;
  min-width: 40%;
}

.image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin: 10px 0 20px 0;
}

.header {
  display: inline-block;
  margin: 15px 20px;
}
